import React, { useState, useEffect, useCallback, useRef } from "react"
import USAMap from "./../../../components/Map/USAMap"
import StateMap from "./../../../components/Map/StateMap"
import pt from "prop-types"
import { Meter, Text, Stack, DataTable, Heading, Grommet, Box, Button, TextInput, DropButton, Avatar } from "grommet"
import { useQueryParam, StringParam } from "use-query-params"
import numeral from "numeral"
import { Map, List } from "grommet-icons"
import { Search } from "grommet-icons"
import { getSuggestions } from "./../utils/events"
import useDebounce from "../../../hooks/useDebounce"
import useNavigate from "../../../hooks/useNavigate"
import { ROUTES } from "../../../routes/routes"
import AddressSearch from "./AddressSearch"
import mapDataFactory from "./../../../components/Map/data/usa-map-dimensions"
import MapUs from "./Map"

const MapView = () => {
  const [suggestionOpen, setSuggestionOpen] = useState(false)
  const [, updateState] = useState()
  const forceUpdate = useCallback(() => updateState({}), [])
  const [query, setQuery] = useState("")
  const [suggestions, setSuggestions] = useState([])
  const boxRef = useRef()
  const agentNameSearchRef = useRef()
  const { debounce } = useDebounce()

  const [mapView, setMapView] = useState(false)
  const [initialized, setInitialized] = useState(false)
  const { navigate } = useNavigate()
  const [selectedAgent, setSelectedAgent] = useState()
  const [viewType, setViewType] = useQueryParam("viewType", StringParam)
  const mapData = mapDataFactory()

  useEffect(() => {
    forceUpdate()
  }, [forceUpdate])

  const onChange = async e => {
    const query = e.target.value
    setQuery(query)
    debounce({
      func: async () => {
        if (!query.trim()) {
          setSuggestions([])
        } else {
          const s = await getSuggestions(query)
          setSuggestions(s)
        }
      },
      wait: 200,
    })()
  }

  const [selectedState, setSelectedState] = useState()

  /* mandatory */

  const mapHandler = event => {
    const stateName = event.target.dataset.name
    setSelectedState(stateName)
  }

  /* optional customization of filling per state and calling custom callbacks per state */
  const statesCustomConfig = () => {
    let config = {}
    const states = Object.keys(mapData)
    states.forEach((s, i) => {
      config[s] = {
        fill: i % 2 == 0 ? "#3C3B6E" : "#B22234",
        clickHandler: event => console.log(`Custom handler ${event.target.dataset}`),
      }
    })
    return config
  }
  return (
    <Box margin="0 auto">
      <Box margin="40px auto">
        <AddressSearch width="xlarge" placeholder="Enter an address, neighborhood, city, or ZIP code" ÃŸ />
      </Box>
      {/* {!selectedState && <USAMap width="1400" height="700" customize={statesCustomConfig()} onClick={mapHandler} />}
      {selectedState && <StateMap width="1400" stateName={selectedState}></StateMap>} */}
      <MapUs></MapUs>
    </Box>
  )
}

export default MapView
