import React, { useState, useEffect, useCallback, useRef } from "react"
import pt from "prop-types"
import { Meter, Text, Stack, DataTable, Heading, Grid, Box, Button, TextInput, DropButton, Avatar } from "grommet"
import { useQueryParam, StringParam } from "use-query-params"
import useNavigate from "../../hooks/useNavigate"

import useDebounce from "../../hooks/useDebounce"
import AgentsView from "./components/AgentsView"
import MapView from "./components/MapView"

const Agents = () => {
  const [suggestionOpen, setSuggestionOpen] = useState(false)
  const [, updateState] = useState()
  const forceUpdate = useCallback(() => updateState({}), [])
  const [query, setQuery] = useState("")
  const [showBanner, setShowBanner] = useState(false)
  const [suggestions, setSuggestions] = useState([])
  const boxRef = useRef()
  const agentNameSearchRef = useRef()
  const { debounce } = useDebounce()

  const [mapView, setMapView] = useState(true)
  const [initialized, setInitialized] = useState(false)
  const { navigate } = useNavigate()
  const [selectedAgent, setSelectedAgent] = useState()
  const [viewType, setViewType] = useQueryParam("viewType", StringParam)

  useEffect(() => {
    forceUpdate()
  }, [forceUpdate])

  useEffect(() => {
    if (!viewType) {
      setViewType(mapView ? "map" : "list")
    } else {
      setMapView(viewType === "map" ? true : false)
    }
    setInitialized(true)
  }, [])

  const handleSwitchView = selectedView => {
    if (viewType === selectedView) {
      return
    }
    setMapView(!mapView)
    setViewType(mapView ? "list" : "map")
  }

  const gridSettings = showBanner => {
    if (showBanner) {
      return {
        rows: ["xxsmall", "small", "large", "xlarge"],
        areas: [
          { name: "baner", start: [0, 0], end: [1, 0] },
          { name: "header", start: [0, 1], end: [1, 2] },
          { name: "main", start: [0, 2], end: [1, 2] },
          { name: "thumbnail", start: [0, 3], end: [1, 3] },
        ],
      }
    } else {
      return {
        rows: ["xlarge", "xlarge"],
        areas: [
          { name: "main", start: [0, 0], end: [1, 0] },
          { name: "thumbnail", start: [0, 1], end: [1, 1] },
        ],
      }
    }
  }

  const settings = gridSettings(showBanner)
  return (
    <Grid rows={settings.rows} columns={["flex"]} gap="small" fill responsive={true} areas={settings.areas}>
      <Box elevation="xsmall" round="xxsmall" background="light-2" gridArea="main" margin={{ bottom: "medium" }}>
        <MapView></MapView>
      </Box>

      {showBanner && (
        <Box pad="small" gridArea="baner" background="light-1" elevation="xsmall">
          Banner
        </Box>
      )}
      {/*   <Box round="xxsmall" gridArea="thumbnail" background="light-2" elevation="xsmall" margin={{ bottom: "medium" }}>
        <AgentsView></AgentsView>
      </Box> */}
    </Grid>
  )
}

export default Agents
