import React from "react"
import PropTypes from "prop-types"
import data from "./data/usa-map-dimensions"
import USAState from "./USAState"

class StateMap extends React.Component {
  clickHandler = stateAbbreviation => {
    this.props.onClick(stateAbbreviation)
  }

  fillStateColor = state => {
    if (this?.props?.customize[state]?.fill) {
      return this.props.customize[state].fill
    }

    return this.props.defaultFill
  }

  stateClickHandler = state => {
    if (this.props.customize && this.props.customize[state] && this.props.customize[state].clickHandler) {
      return this.props.customize[state].clickHandler
    }
    return this.clickHandler
  }

  buildPaths = () => {
    let paths = []
    debugger
    let dataStates = data()[this.props.stateName]
    debugger
    const path = (
      <USAState
        key={this.props.stateName}
        stateName={dataStates.name}
        dimensions={dataStates["dimensions"]}
        state={this.props.stateName}
        fill={this.fillStateColor(this.props.stateName)}
        onClickState={this.stateClickHandler(this.props.stateName)}
      />
    )
    paths.push(path)

    return paths
  }

  render() {
    debugger
    const a = this.buildPaths()
    debugger
    return (
      <svg
        className="us-state-map"
        xmlns="http://www.w3.org/2000/svg"
        width={this.props.width}
        height={this.props.height}
      >
        <title>{this.props.title}</title>
        <g className="outlines">{this.buildPaths()}</g>
      </svg>
    )
  }
}

StateMap.propTypes = {
  onClick: PropTypes.func.isRequired,
  width: PropTypes.number,
  height: PropTypes.number,
  title: PropTypes.string,
  defaultFill: PropTypes.string,
  customize: PropTypes.object,
}

StateMap.defaultProps = {
  onClick: () => {},
  width: 959,
  height: 593,
  defaultFill: "#D3D3D3",
  title: "Blank US states map",
  customize: {},
}

export default StateMap
